
*{
    font-family: "Lucida Console";
}
#mainframe{
    padding-left: 0.5%;
}

#search-button{
    margin-left: 1%;
    padding: 0.4%;
    border-radius: 5px;
    background-color: #0e487d;
    color: #FFFFFF;
    border: none;
}



#search-button:hover {
    background-color: #11599A;
}

#prodnum {
    background-color: #0e487d;
    color: #FFFFFF;
    border: none;
    border-radius: 5px;
    padding:0.25%;
}

table tr:nth-child(odd) td{
    background-color: #828c9e;
}

table tr:nth-child(even) td{
    background-color: #9ba7bd ;
}

th {
    background-color: #0e487d;
    color: #FFFFFF;
}

 th, td{
    border: 1px solid;
    text-align: center;
     padding: 10px;
}

 #headline{
     float: left;
 }

 #logo{
     padding-left: 20px;
 }

 tbody tr:hover td{
     background-color: #0e487d;
 }